.header {
	height: 760px;
	padding-top: 29px;

	background: url('../images/bg-hero.jpg') top center no-repeat;
	background-size: cover;

	@include respond-to(tablets) {
		height: 100vh;

		background-size: auto;
	}
	@include respond-to(handhelds) {
		height: 100vh;
		padding-top: 0;

		background-size: auto;
	}
	&__wrapper {
		display: flex;

		max-width: 1240px;
		margin: 0 auto;

		justify-content: space-between;
	}
}

.header__info {
	margin-top: 150px;
	margin-left: 35px;

	@include respond-to(medium-screens) {
		margin-top: 100px;
	}
	@include respond-to(tablets) {
		margin-top: 65px;
	}
	@include respond-to(handhelds) {
		margin: 0 auto;
		margin-top: 120px;

		text-align: center;
	}
}

.header__title {
	font-family: $raleway;
	font-size: 42px;
	font-weight: 700;
	line-height: 36px;

	padding-left: 5px;

	color: $white;

	@include respond-to(tablets) {
		font-size: 36px;
	}
}

.header__subtitle {
	font-family: $opensans;
	font-size: 22px;
	font-weight: 600;
	line-height: 36px;

	padding-left: 3px;

	color: $white;

	@include respond-to(tablets) {
		font-size: 18px;
	}
}

.header__list {
	font-family: $opensans;
	font-size: 16px;
	font-weight: 400;

	margin-top: 45px;

	color: $white;

	@include respond-to(tablets) {
		font-size: 14px;
	}
	@include respond-to(handhelds) {
		margin-right: 20px;
		margin-left: 20px;

		text-align: left;
	}
	&-item {
		position: relative;

		padding-left: 25px;

		list-style-type: none;
		&:not(:last-child) {
			margin-bottom: 20px;
		}
		&::before {
			position: absolute;
			top: 50%;
			left: -5px;

			width: 7px;
			height: 7px;

			content: '';
			transform: translateY(-50%) rotate(45deg);

			border-top: 1px solid currentColor;
			border-right: 1px solid currentColor;
		}
	}
}



.link.download {
	font-size: 20px;
	font-weight: 600;

	position: relative;

	display: inline-block;

	margin-top: 75px;
	padding: 10px 20px;
	padding-left: 55px;

	text-decoration: none;

	color: $white;
	border-radius: 3px;
	background: url(../images/cloud.png) no-repeat $sea-green;
	background-position: 10px center;

	@include respond-to(tablets) {
		font-size: 16px;
	}
	@include respond-to(handhelds) {
		font-size: 16px;

		margin-top: 50px;
	}
	&::before {
		font-size: 24px;

		position: absolute;
		top: 50%;
		left: 22px;

		transform: translateY(-50%);
	}
	span {
		font-family: $opensans;
	}
}

.header__images {
	position: relative;

	margin-top: 150px;

	@include respond-to(medium-screens) {
		margin-top: 95px;
	}
	@include respond-to(tablets) {
		margin-top: 65px;
	}
	@include respond-to(handhelds) {
		display: none;
	}
}

.header__image {
	position: absolute;
	z-index: 2;
	top: 0;
	right: 185px;

	width: 330px;
	height: 590px;

	background-image: url(../images/screen.png);
	background-repeat: no-repeat;
	background-position: 0 0;
	background-size: 330px 590px;

	@include respond-to(medium-screens) {
		right: 88px;
	}
	@include respond-to(tablets) {
		top: 18px;
		right: 55px;

		width: 270px;
		height: 450px;

		background-size: 270px 450px;
	}
	&:last-child {
		position: absolute;
		z-index: 1;
		top: -60px;
		right: 100px;

		width: 345px;
		height: 649px;

		background-image: url(../images/screen.png);
		background-repeat: no-repeat;
		background-position: 0 0;
		background-size: 345px 649px;

		@include respond-to(medium-screens) {
			right: 0;
		}
		@include respond-to(tablets) {
			top: -42px;
			right: 0;

			width: 265px;
			height: 509px;

			background-size: 265px 509px;
		}
	}
}
