.logo {
	font-family: $raleway;
	font-size: 30px;
	font-weight: 600;
	&__link {
		text-decoration: none;

		color: $sea-green;
	}
}

.nav {
	position: relative;
	z-index: 1000;

	display: flex;

	width: 1240px;
	margin: 0 auto;
	padding: 23px 35px 23px 38px;

	background-color: $white;

	justify-content: space-between;
	align-items: center;
	@include respond-to(medium-screens) {
		width: 769px;
	}
	@include respond-to(tablets) {
		width: 640px;
	}
	@include respond-to(handhelds) {
		position: fixed;
		top: 0;

		width: 100%;
	}
}

.nav__btn {
	position: absolute;
	top: 50%;
	right: 20px;

	display: none;

	width: 18px;
	height: 14px;

	transform: translateY(-50%);

	color: $sea-green;

	@include respond-to(handhelds) {
		display: block;
	}
	&::after {
		position: absolute;
		top: 50%;
		left: 0;

		width: 100%;
		height: 2px;

		content: '';
		transform: translateY(-50%);

		background: currentColor;
		box-shadow: 0 6px 0 0 currentColor,
		0 -6px 0 0 currentColor;
	}
}

.nav.is-active > .nav__links {
	display: block;
}

.nav__links {
	@include respond-to(handhelds) {
		position: fixed;
		z-index: 3;
		top: 80px;
		right: 0;
		left: 0;

		display: none;

		height: 100vh;

		background-color: rgba(0, 0, 0, .7);
	}
}

.nav__links-item {
	font-family: $opensans;
	font-size: 13px;
	font-weight: 700;

	text-decoration: none;
	text-transform: uppercase;

	color: $platinum;

	@include respond-to(handhelds) {
		font-size: 22px;

		display: block;

		padding: 20px;

		transition: .3s;
		text-align: left;

		color: $white;
	}
	&:not(:last-child) {
		margin-right: 40px;

		@include respond-to(tablets) {
			margin-right: 20px;
		}
	}
	&:hover {
		color: $black;
	}
}
