.features {
	max-width: 1240px;
	margin: 0 auto;

	@include respond-to(wide-screens) {
		margin-top: 150px;
	}
	@include respond-to(tablets) {
		margin-top: 60px;
	}
	@include respond-to(handhelds) {
		margin-top: 60px;
	}
}


.features__title {
	@extend %underlined-title;
}

.features__general {
	font-family: $opensans;
	font-size: 18px;
	font-weight: 400;
	line-height: 36px;

	margin-top: 60px;
	padding: 0 150px;

	text-align: center;

	color: $black;

	@include respond-to(handhelds) {
		font-size: 16px;
		line-height: 28px;

		padding: 0 30px;
	}
}

.features__description {
	display: flex;

	margin-top: 85px;
	margin-bottom: 80px;

	@include respond-to(tablets) {
		flex-flow: wrap;
	}
	@include respond-to(handhelds) {
		margin-top: 30px;

		flex-flow: wrap;
	}
}

.features__list {
	margin: 70px 40px 0;

	@include respond-to(tablets) {
		order: 2;
	}
	@include respond-to(handhelds) {
		order: 2;
	}
	@include respond-to(medium-screens) {
		margin: 70px 20px 0;
	}
	&-item {
		padding-right: 20px;

		list-style-type: none;
		&:not(:last-child) {
			margin-bottom: 60px;
		}
	}
}

.features__list-item.icon {
	position: relative;
	&::before {
		font-size: 36px;

		position: absolute;
		top: 0;

		color: $greeny;
	}
}

.feature__name {
	font-family: $raleway;
	font-size: 18px;
	font-weight: 700;
	line-height: 36px;

	display: block;

	margin-bottom: 20px;
	padding-left: 80px;

	color: $black;

	@include respond-to(medium-screens) {
		padding-left: 50px;
	}
}

.feature__description {
	font-family: $opensans;
	font-size: 13px;
	font-weight: 400;
	line-height: 24px;

	display: block;

	margin-left: 80px;

	color: $black;

	@include respond-to(medium-screens) {
		margin-left: 50px;
	}
}

.features__img {
	height: 700px;

	background-image: url(../images/screen.png);
	background-repeat: no-repeat;
	background-position: -48px 0;

	flex: 2 1 50%;
	@include respond-to(medium-screens) {
		flex: 2 1 70%;
	}
	@include respond-to(tablets) {
		background-position: center 0;

		order: 1;
	}
	@include respond-to(handhelds) {
		height: 400px;

		background-position: center 0;
		background-size: 60%;

		order: 1;
	}
}
